.nav{
    top:0;
    position: fixed;
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding: 20px;
    height: 30px;
    z-index: 1;

    transition: 0.5s all ease-in;
}
.nav__black{
    background-color: #111;
}
.nav__logo{
    position: fixed;
    left: 20px;
    width: 100px;
    object-fit: contain;
}
.nav__avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}